// ======================
// Color Variables
// ======================

$primary: #006DB7;
$primary-hover: #197fc4;
$secondary: #37B5FF;
$secondary-hover: #64c6ff;
$light: #F8F8F8;
$gray: #6E6E6E;
$dark: #1D2731;

// ======================
// Shadows
// ======================
$shadow: 0px 2px 8px rgba(0, 0, 0, 0.25);

// ======================
// Breakpoints
// ======================
$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px
) !default;

$breakpoint-xs: 0;
$breakpoint-sm: 576px;
$breakpoint-md: 768px;
$breakpoint-lg: 992px;
$breakpoint-xl: 1200px;

// ======================
// Fonts
// ======================

// Families
$font-roboto: 'Roboto', sans-serif;

// Weights
$font-light: lighter;
$font-regular: normal;
$font-bold: bold;

// ======================
// Animations
// ======================

// Transitions
$transition-smooth-normal: 350ms ease-in;
$transition-smooth-fast: 150ms ease-in;

// Transformations
$transform-scale: scale(1.025);